export const reducer = (state, {type, payload}) =>{
    switch(type){
        case 'SET_PRODUCTS':
            return {
                ...state,
                items: payload || [],
                loading: false,
            };
        case 'ADD_ORDER':
            const itemIndex = state.orders.findIndex(order=>order.id===payload.id)
            let newOrder = null;
            if(itemIndex<0){
                const newItem = {
                    ...payload,
                    quantity:1,
                }
                newOrder = [...state.orders, newItem];
            }
            else{
                newOrder = state.orders.map((order,index) => {
                    if(index===itemIndex){
                        return{
                            ...order,
                            quantity:order.quantity + 1,
                        }
                    }
                    else{
                        return order;
                    }});  
            }
            return {
                ...state,
                orders: newOrder,
                alertName: payload.name,
            } ;
        case 'REMOVE_ORDER':
            return {
                ...state,
                orders: state.orders.filter(order=>order.id!==payload.id),
            };
        case 'HANDLE_BASKET_SHOW':
            return {
                ...state,
                isBasketShow: !state.isBasketShow,
            };
        case 'HANDLE_QUANTITY':
            return {
                ...state,
                orders: state.orders.map( order => {
                    if(order.id===payload.id){
                        return{
                            ...order,
                            quantity:payload.value,
                        };
                    }
                    else{
                        return order;
                    }})
                    .filter(order=>order.quantity>0),
            };
        case 'CLOSE_ALERT':
            return {
                ...state,
                alertName:'',
            };
        default:
            return state;
    }
}